import React from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import Loading from '../../Components/Loading'

const Container = styled.div`
    border:1px solid yellow;
    max-width:90%;
    margin: 0 auto;
    text-align:center;
    @media screen and (max-width:768px){
    max-width:90%;
    }
`
const Box = styled.p`
    border:1px solid yellow;
    color:white;
    padding:10px;
    font-size:25px;
    @media screen and (max-width:768px){
        font-size:20px;
    }
`
const Title = styled.p`
    font-size:27px;
    font-weight:700;
    padding:10px;
    color:black;
    @media screen and (max-width:768px){
        padding:3px;
        font-size:22px;
    }

`
const PayOn = styled.p`
    padding:10px;
    @media screen and (max-width:768px){
        font-size:18px;
        font-weight:500;
        padding:3px;
    }
`

const CoinExchangesPresenter = ({ data, error, loading }) => {
    return (
        loading ? <Loading padDown={'-35px'} /> : error ? <div>error</div> :
            (data && data.length > 0 && data.map((i, index) => (
                <Container key={index}>
                    <Box>
                        <Title>
                            {i.name}
                        </Title>
                        <PayOn>
                            {i.fiats && i.fiats.length === 0 && <span>Nothing.</span>}
                            {i.fiats && i.fiats.length > 0 && <span style={{ fontWeight: 500, color: 'white' }}>Pay On</span>}
                            {i.fiats && i.fiats.length > 0 && i.fiats.map(i => <span style={{ color: 'yellow', fontWeight: 700 }}> {i.symbol} </span>)}
                        </PayOn>
                    </Box>
                </Container>)
            )
            )
    )
}

//name,fiats
CoinExchangesPresenter.propTypes = {
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    data: PropTypes.array
}
export default CoinExchangesPresenter