import React, { Component } from 'react'
import CoinExchangesPresenter from './CoinExchangesPresenter'
import { CoinApi } from '../../api'

export default class CoinExchangesContainer extends Component {
    state = {
        loading: true,
        error: null,
        data: null
    }
    async componentDidMount() {
        try {
            const { path } = this.props.match
            const id = path.split('/')[2]
            const { data } = await CoinApi.exchangesById(id)
            this.setState({ data })
        } catch (error) {
            this.setState({ error })
        } finally {
            this.setState({ loading: false })
        }
    }
    render() {
        const { data, error, loading } = this.state
        return (
            <CoinExchangesPresenter data={data} error={error} loading={loading} />
        )
    }
}
