import React, { Component } from 'react'
import CoinDetailPresenter from './CoinDetailPresenter'
import { CoinApi } from '../../api'

export default class CoinDetailContainer extends Component {
    state = {
        loading: true,
        error: null,
        data: null
    }

    async componentDidMount() {
        try {
            const { match: { params: { id } } } = this.props
            const { data } = await CoinApi.coinById(id)
            this.setState({ data })
        } catch (error) {
            this.setState(error)
        } finally {
            this.setState({ loading: false })
        }
    }

    render() {
        const { loading, error, data } = this.state
        return (
            <CoinDetailPresenter loading={loading} error={error} data={data} />
        )
    }
}

