import React from 'react'
import styled from 'styled-components'

const doading = styled.div`
    text-align:center;
    color:white;
    margin:auto;
    font-size:30px;
    font-weight:600;
    padding:200px 0 0 0;
    animation: animation 2s infinite;

    @keyframes animation {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
    }
    @media screen and (max-width:768px){
        padding: 50px 0 0 0;
        font-size: 25px;
    }
`
const Loading = styled.div`
h1 {
    position: relative;
    color: rgba(0, 0, 0, .3);
    font-size: 5em;
    line-height:5em;
    text-align:center;
}
h1:before {
    content: attr(data-text);
    line-height:5em;
    max-width:100%;
    position: absolute;
    overflow: hidden;
    white-space: nowrap;
    color: #fff;
    animation: loading 5s linear infinite;
}
@keyframes loading {
    0% {
        max-width: 0;
    }
}
@media screen and (max-width:768px){
    margin:0;
    padding:0;
    position: relative;
    h1{
    font-size:25px;
    line-height:30px;
    }
    h1:before{
    line-height:30px;
    }
}
`

export default ({ padDown }) => {
    return (
        <>
            <Loading style={{ top: padDown }}><br /><br />
                <h1 data-text="I'm a Responsive Web Page!">I'm a Responsive Web Page!</h1>
            </Loading >
        </>
    )
}