import React from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import Loading from '../../Components/Loading'

const Container = styled.div`
    border:1px solid yellow;
    max-width:90%;
    margin: 0 auto;
    text-align:center;
    display:grid;
    grid-template-columns: repeat(auto-fill,25%);
    @media screen and (max-width:768px){
    grid-template-columns: repeat(auto-fill,50%);
    max-width:90%;
    }
`
const Name = styled.p`
    color:white;
    padding:10px;
    border:1px solid yellow;
    font-size:25px;
    a{
        color:inherit;
        cursor: pointer;
    }
    @media screen and (max-width:768px){
        font-size:20px;
    }
`


const CoinMarketsPresenter = ({ data, error, loading }) => {
    return (
        loading ? <Loading padDown={'-35px'} /> : error ? <div>error</div> :
            (
                <Container>
                    {data && data.length > 1000 && data.slice(0, 1000).map((i, index) => <Name key={index}><a target="_blank" rel="noopener noreferrer" href={i.market_url}>{i.exchange_name}</a></Name>)}
                    {data && data.length > 0 && data.length < 1000 && data.map((i, index) => <Name key={index}><a target="_blank" rel="noopener noreferrer" href={i.market_url}>{i.exchange_name}</a></Name>)}
                </Container>
            )
    )
}


CoinMarketsPresenter.propTypes = {
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    data: PropTypes.array
}
export default CoinMarketsPresenter