import React, { useState } from 'react'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import Loading from '../../Components/Loading'
import { Route, NavLink } from 'react-router-dom'
import CoinExchanges from '../CoinExchanges'
import CoinMarkets from '../CoinMarkets'

const Container = styled.div`
    max-width:50%;
    margin: 0 auto;
    padding-top: 50px;
    @media screen and (max-width:768px){
    padding-top: 10px;
    max-width:95%;
    }
`
const Title = styled.div`
    font-size:27px;
    font-weight:700;
    padding:10px 0;
    color:yellow;
    margin:auto;
    width:95%;
`
const Description = styled.div`
    font-size:17px;
    color:white;
    line-height:1.3;
    padding: 5px 0 20px;
    width:95%;
    margin:auto;
    cursor: pointer;
`

const ItemBox = styled.div`
    &:last-child{border-bottom:unset}
    border-bottom:2px solid rgba(255,255,255,0.6);
    font-size:18px;
    color:white;
    padding:5px 0;
    display:flex;
    justify-content:center;
    width:100%;
    margin:auto;
    @media screen and (max-width:768px){
        width:80%;
        margin:auto
    }
`
const Item = styled.p`
    font-size:20px;
    font-weight:700;
    flex:6;
    text-align:center;
    padding:5px;
    @media screen and (max-width:768px){
        flex:unset;
        font-size:16px;
        width:250px;
        padding:3px 0;
    }
`
const ItemValue = styled.p`
    flex:6;
    text-align:center;
    padding:5px;
    font-weight:500;
    color:white;
    border-left:2px solid rgba(255,255,255,0.6);
    @media screen and (max-width:768px){
        flex:unset;
        font-size:16px;
        width:250px;
        padding:3px 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
`
const ButtonContainer = styled.form`
    font-size:17px;
    color:white;
    line-height:1.3;
    padding: 30px 0 70px;
    width:90%;
    margin:auto;
    display:flex;
    justify-content:space-between;
    @media screen and (max-width:768px){
    padding:30px 0;
    }
`
const ButtonLink = styled(NavLink)`
    background:white;
    text-decoration:none;
    font-weight:600;
    font-size:20px;
    color:black;
    width: 47%;
    line-height:40px;
    height:40px;
    text-align:center;
    display:block;
    border-radius:7px;
    border: 2px solid black;
    transition:0.2s linear;
    &:hover{
        color:red;
    }
`
const CoinDetailPresenter = ({ loading, error, data }) => {
    const [clicked, setClicked] = useState(true)
    const onClick = (e) => {
        e.preventDefault()
        setClicked(prev => !prev)
    }
    return (loading ? <Loading /> : error ? <div>error</div> :
        <Container>
            <Title>{data.name} / {data.symbol}</Title>
            <Description onClick={onClick}>
                {!data.description && 'No description!'}
                {data.description && data.description.length <= 150 && data.description}
                {data.description && data.description.length > 150 && clicked && <span>{data.description.substring(0, 100) + '... '}<span style={{ color: 'black' }}> view more</span></span>}
                {data.description && data.description.length > 150 && !clicked && <span>{data.description} <span style={{ color: 'black' }}> click for shrink</span></span>}
            </Description>
            <ItemBox>
                <Item>Rank</Item><ItemValue>{data.rank}</ItemValue>
            </ItemBox>
            <ItemBox>
                <Item>Open Source</Item><ItemValue>{data.open_source}</ItemValue>
            </ItemBox>
            <ItemBox>
                <Item>Proof Type</Item><ItemValue>{data.proof_type}</ItemValue>
            </ItemBox>
            <ItemBox>
                <Item>Structure</Item><ItemValue>{data.org_structure}</ItemValue>
            </ItemBox>
            <ButtonContainer>
                <ButtonLink to={`/coins/${data.id}/markets`}
                    activeStyle={{
                        background: "black",
                        color: "white"
                    }}>MARKETS</ButtonLink>
                <ButtonLink to={`/coins/${data.id}/exchanges`} activeStyle={{
                    background: "black",
                    color: "white"
                }} >EXCHANGES</ButtonLink>
            </ButtonContainer>
            <Route path={`/coins/${data.id}`} exact component={null} />
            <Route path={`/coins/${data.id}/markets`} exact component={CoinMarkets} />
            <Route path={`/coins/${data.id}/exchanges`} exact component={CoinExchanges} />
        </Container >)
}

CoinDetailPresenter.propTypes = {
    loading: PropTypes.bool.isRequired,
    error: PropTypes.string,
    data: PropTypes.object
}

export default CoinDetailPresenter